import React from 'react'
import axios from 'axios'
import AppContext from './context'
import App from './App'
import { produce } from 'immer'
import * as bs from 'react-bootstrap'


/** The context provider for our app */
export default class AppProvider extends React.Component {

    constructor(props) {
        super(props)
        this.actions = {
            addToCart: this.addToCart,
            removeFromCart: this.removeFromCart,
            addQuantity: this.addQuantity,
            subQuantity: this.subQuantity,
            updateCartTotal: this.updateCartTotal,
            getCartTotal: this.getCartTotal,
            clearCart: this.clearCart,
        }
        this.state = {
            categories: null,
            products: null,
            campaigns: null,
            cart: {},
            cartCount: 0,
            cartTotal: 0,
        }
    }


    // The cart is an object of objects {{pid, qty}, {pid, qty}, {pid, qty}}
    addToCart = (pid) => {

        // set the new cart
        // Inside of the following produce call, it's okay to modify state, outside you shouldn't
        this.setState(state => produce(state, draft => { // draft is the current state
            if(draft.cart[pid]){
                const qty = draft.cart[pid]
                draft.cart[pid] = qty + 1
                draft.cartCount = draft.cartCount + 1
            }
            else{
                draft.cart[pid] = 1
                draft.cartCount = draft.cartCount + 1
            }
        }))
    }

    removeFromCart = (pid) => {

        // set the new cart
        this.setState(state => produce(state, draft => { // draft is the current state
            if(draft.cart[pid]){
                draft.cartCount = draft.cartCount - draft.cart[pid]
                delete draft.cart[pid]
            }
        }))
    }

    addQuantity = (pid) => {

        // set the new cart
        this.setState(state => produce(state, draft => { // draft is the current state
            if(draft.cart[pid]){
                const qty = draft.cart[pid]
                draft.cart[pid] = qty + 1
                draft.cartCount = draft.cartCount + 1
            }
        }))
    }

    subQuantity = (pid) => {

        // set the new cart
        this.setState(state => produce(state, draft => { // draft is the current state
            if(draft.cart[pid]){
                if(draft.cart[pid] <= 1){
                    delete draft.cart[pid]
                    draft.cartCount = draft.cartCount - 1
                }
                else{
                    const qty = draft.cart[pid]
                    draft.cart[pid] = qty - 1
                    draft.cartCount = draft.cartCount - 1
                }
            }
        }))
    }

    updateCartTotal = (total) => {

        this.setState(state => produce(state, draft => { // draft is the current state
            
            draft.cartTotal = total

        }))
    }

    getCartTotal = () => {

        const total = this.state.cartTotal

        return{
            total
        }
    }    

    clearCart = () => {

        this.setState(state => produce(state, draft => { // draft is the current state
            
            draft.cartTotal = 0
            draft.cartCount = 0
            draft.cart = {}

        }))
    }

    render() {

        if(!this.state.categories){
            return <div>
                <bs.Spinner animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </bs.Spinner>
            </div>
        }
        
        if(!this.state.products){
            return <div>
                <bs.Spinner animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </bs.Spinner>
            </div>
        }

        // Unsure if this is necessary
        if(!this.state.cart){
            return <div>
                <bs.Spinner animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </bs.Spinner>
            </div>
        }

        if(!this.state.campaigns){
            return <div>
                <bs.Spinner animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </bs.Spinner>
            </div>
        }

        return (
            <AppContext.Provider value={{...this.state, ...this.actions}}>
                <App />
            </AppContext.Provider>
        )
    }

    async componentDidMount() {
        const cats = await axios.get('http://localhost:8000/api/category')
        const prods = await axios.get('http://localhost:8000/api/product')
        const camps = await axios.get('http://localhost:8000/api/campaign')
        // const cats = await axios.get('/api/category')
        // const prods = await axios.get('/api/product')
        // const camps = await axios.get('/api/campaign')
        // const initial_cart = {
        //     12123 : 2,
        //     22622 : 1
        // }


        this.setState({
            ...this.state,
            categories: cats.data,
            products: prods.data,
            campaigns: camps.data,
            //cart: initial_cart
        })
    }

}