import React from 'react'
import AppContext from './context.js'
import { useHistory } from 'react-router-dom'
import * as bs from 'react-bootstrap'


function ShoppingCart(props) {

    const state = React.useContext(AppContext)

    // Up here I'm going to create an array of Cart objects [{product.name, qty, product.price, extended}, {}, {}]
    // by looping through the state.cart objects {{id, qty}, {id, qty}, {id, qty}}
    // Do the math for extended
    // Calculate the total cart price

    let data = []
    let history = useHistory()
    let total = 0

    Object.keys(state.cart).forEach(function(key) {
        
        let product = null

        for (const p of state.products) {
            let num = '' + p.id
            if (num === key) {
                product = p
            }
        }

        let extended = state.cart[key] * product.price

        if(product){
            data.push({
                'id': product.id,
                'filename': product.filename,
                'name': product.name,
                'quantity': state.cart[key],
                'price': product.price,
                'extended': extended
            })
        }

        total = total + extended
    })

    return (
        <div className="m-4 table-responsive text-nowrap">
            <p>Shopping Cart</p>
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th></th>
                        <th>Product</th>
                        <th>Quantity</th>
                        <th>Price</th>
                        <th>Extended</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {data.map((product, idx) => {
                        return <tr key={idx}>
                            <td>
                                <img
                                    alt={product.name}
                                    //src={"/products/" + product.filename + "-1.png"}
                                    src={`${process.env.PUBLIC_URL}/products/${product.filename}-1.png`}
                                    className="border rounded mt-1 mx-1"
                                    style={{
                                        height: "100px",
                                        width: "100px"
                                    }}
                                />
                            </td>
                            <td>{product.name}</td>
                            <td>
                                <bs.Button
                                    variant="secondary"
                                    onClick={e => {
                                        state.subQuantity(product.id)
                                    }}
                                >
                                    -
                                </bs.Button>
                                &nbsp;
                                &nbsp;
                                {product.quantity}
                                &nbsp;
                                &nbsp;
                                <bs.Button
                                    variant="secondary"
                                    onClick={e => {
                                        state.addQuantity(product.id)
                                    }}
                                >
                                    +
                                </bs.Button>
                            </td>
                            <td>{product.price}</td>
                            <td>{product.extended}</td>
                            <td>
                                <bs.Button
                                    variant="secondary"
                                    onClick={e => {
                                        state.removeFromCart(product.id)
                                    }}
                                >
                                    Remove
                                </bs.Button>
                            </td>
                        </tr>
                    })}
                </tbody>
            </table>
            <p>Total: {total}</p>
            <bs.Button
                variant="secondary"
                onClick={e => {
                    history.push("/checkout")
                    state.updateCartTotal(total)
                }}
            >
                Checkout
            </bs.Button>
        </div>
    )
}

export default ShoppingCart