import React from 'react'
import { useRouteMatch } from 'react-router-dom'
import { useHistory } from 'react-router-dom'
import AppContext from './context.js'
import * as bs from 'react-bootstrap'


function ProductDetail(props) {

    const state = React.useContext(AppContext)

    const match = useRouteMatch("/product/:id")
    const [imgIdx, setImgIdx] = React.useState("1")

    let product = null
    let num = ''
    let history = useHistory()

    for (const p of state.products) {
        num = '' + p.id
        if (num === (match.params.id)) {
            product = p
        }
    }

    if (!product) {
        return (<h2>Product Not Found</h2>)
    }

    return (
        <div className="m-4">
            <div
                className="float-right border rounded m-0 p-0"
                style={{
                    width: '300px',
                    height: '300px'
                }}
            >
                <img
                    alt={product.name}
                    //src={"/products/" + product.filename + "-" + imgIdx + ".png"}
                    src={`${process.env.PUBLIC_URL}/products/${product.filename}-${imgIdx}.png`}
                    
                    style={{
                        width: '300px',
                        height: '300px'
                    }}
                    className="w-100"
                />
                <div>
                    {["1", "2", "3", "4"].map(img_index => (
                        <img
                            key={product.id + img_index}
                            alt={product.name}
                            src={`${process.env.PUBLIC_URL}/products/${product.filename}-${img_index}.png`}
                            className="border rounded mt-3 mx-1"
                            style={{
                                height: "30px",
                                width: "30px"
                            }}
                            onMouseEnter={e => {
                                setImgIdx(img_index)
                            }}
                        />
                    ))}
                </div>
            </div>
            <h1>{product.name}</h1>
            <h2>${product.price}</h2>
            <p> {product.description}</p>

            <bs.Button
                variant="warning"
                onClick={e => {
                    state.addToCart(product.id)
                    history.push("/cart")
                }}
            >
                Add To Cart
            </bs.Button>

        </div>

    )
}

export default ProductDetail