import React from 'react'
import * as bs from 'react-bootstrap'
import { Link } from "react-router-dom"


function ProductCard(props) {
    return (
        <bs.Card size="lg" color="secondary">
            <bs.Card.Img 
                variant="top" 
                //src={"/products/" + props.product.filename + "-1.png"} 
                src={`${process.env.PUBLIC_URL}/products/${props.product.filename}-1.png`}
            />
            <bs.Card.Body>
                <bs.Card.Title>
                    {props.product.name}
                </bs.Card.Title>
                <bs.Card.Text>
                    ${props.product.price}
                </bs.Card.Text>
                <Link to={"/product/" + props.product.id} className="btn btn-primary position-absolute" style={{ top: '5px', right: '10px' }}>
                    Details
                </Link>
            </bs.Card.Body>
        </bs.Card>
    )
}


export default ProductCard